<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input
            v-model="phone"
            class="inp"
            maxlength="11"
            placeholder="请输入手机号码"
            type="text"
          />
        </div>
        <div class="form-item">
          <input
            v-model="picCode"
            class="inp"
            maxlength="5"
            placeholder="请输入图形验证码"
            type="text"
          />
          <img @click="getPic" v-if="picUrl" :src="picUrl" alt="" />
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="请输入短信验证码" type="text" />
          <button @click="getCode">
            {{
              second === totalSeconds ? "获取验证码" : second + "s后重新发送"
            }}
          </button>
        </div>
      </div>

      <div @click="loginFn" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
import { getPic, getCode, validLogin } from '@/api/login'
import { mapMutations } from 'vuex'
export default {
  name: 'LoginIndex',
  data () {
    return {
      picUrl: '',
      picKey: '',
      picCode: '',
      phone: '', // 手机号码
      totalSeconds: 60, // 倒计时总秒数
      second: 60, // 倒计时剩余秒数
      timer: null, // 定时器id
      msgCode: '' // 短信验证码
    }
  },
  created () {
    this.getPic()
  },
  methods: {
    ...mapMutations('user', ['setUserInfo']),
    async loginFn () {
      if (!this.validPhonePic()) {
        return
      }
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast('请输入正确的手机验证码')
        return
      }
      const res = await validLogin(this.phone, this.msgCode)
      console.log(res.data)
      this.setUserInfo(res.data)
      console.log(this.$store.state.user.userInfo)
      this.$toast('登录成功')
      // console.log(this.$route.query.returnPath)
      if (this.$route.query.returnPath) {
        // 有returnPath参数，跳转到原页面，replace不会增加历史记录
        // console.log('有returnPath')
        this.$router.replace(this.$route.query.returnPath)
      } else {
        this.$router.replace('/')
      }
    },
    validPhonePic () {
      // 验证手机号码和图形验证码
      if (!/^1[3-9]\d{9}$/.test(this.phone)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }
      return true
    },
    async getCode () {
      if (!this.validPhonePic()) {
        return
      }
      // 获取手机验证码
      if (!this.timer && this.second === this.totalSeconds) {
        await getCode(this.phone, this.picKey, this.picCode)
        this.$toast('验证码已发送，请注意查收')
        this.timer = setInterval(() => {
          this.second--
          if (this.second <= 0) {
            this.second = this.totalSeconds
            clearInterval(this.timer)
            this.timer = null
          }
        }, 1000)
      }
    },
    async getPic () {
      const res = await getPic()
      console.log(res)
      this.picUrl = res.data.base64
      this.picKey = res.data.key
    }
  },
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
